import React,{ PureComponent } from 'react'
import { connect } from 'react-redux'
import importItem from '../Pagination'
import { BrowserRouter,withRouter } from 'react-router-dom'

const { actions,styles } = importItem
const { Show } = styles

class Pagination extends PureComponent{
    constructor(props){
        super(props)
    }
    render() {
     return(
         <BrowserRouter>
            <Show>
                <nav className="pagination-outer" aria-label="Page navigation">
                    <ul className="pagination justify-content-center align-items-center">
                        <li className={this.props.paginationObjProps.hasPage ? "page-item" : "page-item disabled"}>
                            <a className={this.props.paginationObjProps.hasPage ? "page-link page-link-active" : "page-link"} href="#"
                            aria-label="Previous" onClick={this.getPage.bind(this,this.props.paginationObjProps.currentPage - 1)}>
                                <span aria-hidden="true">
                                    <i className="far fa-chevron-double-left"></i>
                                </span>
                            </a>
                        </li>
                        {(this.renderPage(this.props.paginationObjProps.pageTotal,this.props.paginationObjProps.currentPage))}
                        <li className={this.props.paginationObjProps.hasNext ? "page-item" : "page-item disabled"}>
                            <a className={this.props.paginationObjProps.hasNext ?"page-link page-link-active" : "page-link"} href="#" 
                            aria-label="Next" onClick={this.getPage.bind(this,this.props.paginationObjProps.currentPage + 1)}>
                            <span aria-hidden="true">
                                <i className="far fa-chevron-double-right"></i>
                            </span>
                            </a>
                        </li>
                    </ul>
                </nav>
            </Show>
        </BrowserRouter>
     )   
    }
    componentDidMount(){
        
    }
    componentWillUnmount(){
        
    }
    renderPage(total,currentPage){
        let array = []
        if(window.innerWidth > 768){
            if(total <= 10){
                for(let x = 1;x <= total;x++){
                    array.push(
                        <li key={x}>
                            <a className={currentPage === x ? "page-link active" : "page-link"} href="#" onClick={this.getPage.bind(this,x)}>{x}</a>
                        </li>
                    )
                }
            }else if(total >= 10 && currentPage <= 10){
                for(let x = 1;x <= 10;x++){
                    array.push(
                        <li key={x}>
                            <a className={currentPage === x ? "page-link active" : "page-link"} href="#" onClick={this.getPage.bind(this,x)}>{x}</a>
                        </li>
                    )
                }
            }else if(currentPage >= 10){
                for(let x = 11;x <= total;x++){
                    array.push(
                        <li key={x}>
                            <a className={currentPage === x ? "page-link active" : "page-link"} href="#" onClick={this.getPage.bind(this,x)}>{x}</a>
                        </li>
                    )
                }
            }
        }else{
            if(total <= 6){
                for(let x = 1;x <= total;x++){
                    array.push(
                        <li key={x}>
                            <a className={currentPage === x ? "page-link active" : "page-link"} href="#" onClick={this.getPage.bind(this,x)}>{x}</a>
                        </li>
                    )
                }
            }else if(total >= 6 && currentPage <= 6){
                for(let x = 1;x <= 6;x++){
                    array.push(
                        <li key={x}>
                            <a className={currentPage === x ? "page-link active" : "page-link"} href="#" onClick={this.getPage.bind(this,x)}>{x}</a>
                        </li>
                    )
                }
            }else if(currentPage >= 6 && currentPage <= 12){
                for(let x = 7;x <= 12;x++){
                    array.push(
                        <li key={x}>
                            <a className={currentPage === x ? "page-link active" : "page-link"} href="#" onClick={this.getPage.bind(this,x)}>{x}</a>
                        </li>
                    )
                }
            }else if(currentPage >= 12 && currentPage <= 18){
                for(let x = 13;x <= 18;x++){
                    array.push(
                        <li key={x}>
                            <a className={currentPage === x ? "page-link active" : "page-link"} href="#" onClick={this.getPage.bind(this,x)}>{x}</a>
                        </li>
                    )
                }
            }else if(currentPage > 18 && currentPage <= 24){
                for(let x = 19;x <= 24;x++){
                    array.push(
                        <li key={x}>
                            <a className={currentPage === x ? "page-link active" : "page-link"} href="#" onClick={this.getPage.bind(this,x)}>{x}</a>
                        </li>
                    )
                }
            }else if(currentPage >= 24){
                for(let x = 24;x <= total;x++){
                    array.push(
                        <li key={x}>
                            <a className={currentPage === x ? "page-link active" : "page-link"} href="#" onClick={this.getPage.bind(this,x)}>{x}</a>
                        </li>
                    )
                }
            }
        }
        return array
    }
    getPage(currentPage,el){
        el.preventDefault();
        this.props.paginationPartProps(currentPage)
    }
}

const reactReduxStateToProps = state => {
    return{
        
    }
}

const reactReduxDispatchToProps = dispatch => {
    return{
       
    }
}

export default withRouter(
    connect(reactReduxStateToProps,reactReduxDispatchToProps)(Pagination)
)