import { combineReducers } from 'redux-immutable'
import mainReducer from '../conponents/Main'
import musicReducer from '../conponents/MusicItemList'
import musicVideoReducer from '../conponents/MusicVideoItemList'
import movieReducer from '../conponents/MovieItemList'
import podcastReducer from '../conponents/PodcastItemList'
import ebookReducer from '../conponents/EbookItemList'
import softwareReducer from '../conponents/SoftwareItemList'
import renderSingleForMusic from '../conponents/RenderSingleForMusic'
import renderSingleForMusicVideo from '../conponents/RenderSingleForMusicVideo'
import renderSingleForMovie from '../conponents/RenderSingleForMovie'
import renderSingleForPodcast from '../conponents/RenderSingleForPodcast'
import renderSingleForSoftware from '../conponents/RenderSingleForSoftware'
import dialogReducer from '../conponents/Dialog'
import paginationReducer from '../conponents/Pagination'

export default combineReducers({
    main:mainReducer.reducer,
    musicItem:musicReducer.reducer,
    musicVideoItem:musicVideoReducer.reducer,
    movieItem:movieReducer.reducer,
    podcastItem:podcastReducer.reducer,
    ebookItem:ebookReducer.reducer,
    softwareItem:softwareReducer.reducer,
    renderSingleForMusic:renderSingleForMusic.reducer,
    renderSingleForMusicVideo:renderSingleForMusicVideo.reducer,
    renderSingleForMovie:renderSingleForMovie.reducer,
    renderSingleForPodcast:renderSingleForPodcast.reducer,
    renderSingleForSoftware:renderSingleForSoftware.reducer,
    dialog:dialogReducer.reducer,
    pagination:paginationReducer.reducer
})